.map-page-container {
  @include flexbox(column nowrap);
  justify-content: space-between;
  align-items: flex-start;

  .map-page-title {
    @include flex(0 0 50px);
    font-size: 28px;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    margin-top: 40px;
    margin-bottom: 20px;
    font-weight: 700;
  }

  .map-page-buttons {
    @include flex(0 0 50px);
    position: relative;
    top: 0px;
    margin-right:-5px;
    align-self: flex-end;
    z-index: 1000;
  }

  .map-page-tabs {
    width: 100%;
    margin-top: -35px;
      
    height: 920px;
    @media (max-width: 1199px) { height: 1100px; }
    @media (max-width: 992px) { height: 1550px; }
    @media (max-width: 768px) { height: 1940px; }
    @media (max-width: 496px) { height: 2000px; }

    .content {
      @include flexbox(row nowrap);
      justify-content: space-between;
      align-items: flex-start;
      height: 76%;

      .left-pane {
        @include flex(0 3 800px);

        background: transparent;
        padding: 30px 0px 30px 0px;
        max-height: 800px;

        @include flexbox(column nowrap);
        justify-content: space-between;
        align-items: center;

        .map-header {
          @include flex(1);
          align-self: flex-start;
          width: 380px;

          @include flexbox(column nowrap);
          justify-content: flex-start;
          align-content: flex-start;

          .map-title {
            font-size: 16px;
            font-style: italic;
            margin-left: 3px;  
          }
        }

        .map-container {
          @include flex(0 3 460px);
          width: 100%;
          background: transparent;
          margin-top:35px;
        }

      }

      .legend-container {
        @include flex(0 0 0px);
        width: auto;
        background: transparent;
        border: white;
        position: relative;
        bottom: 45px;
        div {
          background: transparent;
        }
        .legend-display-tab {
          background: #555; 
          width: 140px;
          margin-left: auto;
          margin-right: auto;
          border: 1px solid #ccc;
        }
        .legend-content{
          padding: 13px;
          border: 1px solid #ccc;
        }
      }

      .info-container {
        background: $lightcolor;
        color: $darkcolor;
        border: 1px solid #ccc;
        width: auto;
        div {
          color: $darkcolor;
          background: transparent;
        }
      }
        
      .country-option-column {
        margin-top:-1px; 
        width:auto;
      }
        
      .map-and-search-container {
        @include flex(1 0 350px);
        background: transparent;
        align-self: flex-start;

        padding:100px;

        padding: 20px;
        border: $light-border;

        .selected-country-container {
          width: 100%;

          .selected-country-name {
            width: 100%;
            height: auto;
          }
        }

        .search-container {
          position: relative;

          .live-search-results-container {

            .live-search-results-contents {
              background: white;
              position: absolute;
              top: 194px;
            }
          }
        }

        div,label {
          &:not(.slider-handle) {
            background: transparent;
          }
          .slider-handle {
            background-image: linear-gradient(to bottom,#337ab7 0,#2e6da4 100%);
          }
        }
      }

      div,label {
        background: transparent;
      }

      .slider-tick-vert {
        background-color: #999 !important;
      }

      .paginate_button {
        &.dark {
          &.active {
            a {
              background-color: $darkcolor;
            }
          }
          a {
            border: 1px solid #ccc;
            color: #ccc;
            background-color: #36404d;
          }
        }
      }

      .table-content {
        @include flexbox(row wrap);
        justify-content: space-between;
        align-items: flex-start;

        .left-table,.right-table {
          padding: 10px;
          @include flex(1 0 400px);

          @include flexbox(column nowrap);
          justify-content: flex-start;
          align-items: center;

          div.dataTables_wrapper {
            width: 100%;
          }

        }
      }

      table {
        width: 100%;

        td,tr {
          border: 1px solid #666666;
        }
        td:nth-child(1) {
          border-right: none;
        }
        td:nth-child(2) {
          border-left: none;
        }

        thead {
          td,tr {
            background-color: #eaeaea;
            color: black;
            &.dark {
              background-color: #36404d;
              color: white;
            }
          }
        }
        tbody {
          td,tr {
            background-color: white;
            color: black;
            &.dark {
              background-color: $darkcolor;
              color: white;
            }
          }
        }
      }
    }

  }
}


